import "./addTheatre.css";
import { React, useState } from "react";
import { useHistory } from "react-router-dom";
import Header from "../../generalComponents/header/header";
import Footer from "../../generalComponents/footer/footer";

const AddTheatre = () => {
  let [theatreName, setTheatreName] = useState("");
  let [managerName, setManagerName] = useState("");
  let [number, setNumber] = useState("");
  let [technology, setTechnology] = useState("");
  let [city, setCity] = useState("");
  let [theatreImg, setTheatreImg] = useState("");

  let userId =localStorage.getItem("id")
  const history = useHistory();
  const addTHeatre = async (event) => {
    event.preventDefault();
    let data = {
      theatre_name: theatreName,
      managerName: managerName,
      number: number,
      technology: technology,
      city: city,
      img: theatreImg,
      movie:"",
      showTiming:["10:00","14:00","18:00","22:00"],
      userID:userId,
      admin_verification: "",
    };

    let url = "http://localhost:5000/addTheatre/addTheatre";
    let response = await fetch(url, {
      method: "POST",
      body: JSON.stringify(data),
      headers: {
        "Content-Type": "application/json",
      },
    });
    let Fetchdata = await response.json();

    if (Fetchdata.status === 200) {
      alert("Theatre added successfully");
      history.replace("/HomepageAgent");
    }
    setTheatreName("");
    setManagerName("");
    setNumber("");
    setTechnology("");
    setCity("");
    setTheatreImg("");
  };

  return (
    <div>
      <Header />
      <div className="body">
        <div className="container addTHeatre-container">
          <h1 className="heading">Add Theatre</h1>

          <form onSubmit={addTHeatre} method="POST">
            <div className="group">
              <input
                id="theatreName"
                className="theatreName addTheatre-input"
                type="text"
                onChange={(e) => setTheatreName(e.target.value)}
                required
              />
              <span className="highlight"></span>
              <span className="bar"></span>
              <label className="addTheatre-label">Theatre Name</label>
            </div>

            <div className="group">
              <input
                id="managerName"
                className="managerName addTheatre-input"
                type="text"
                onChange={(e) => setManagerName(e.target.value)}
                required
              />
              <span className="highlight"></span>
              <span className="bar"></span>
              <label className="addTheatre-label">Branch Manager Name</label>
            </div>
            <div className="group">
              <input
                id="number"
                className="number addTheatre-input"
                type="text"
                onChange={(e) => setNumber(e.target.value)}
                required
              />
              <span className="highlight"></span>
              <span className="bar"></span>
              <label className="addTheatre-label">Phone number</label>
            </div>

            <div className="group">
              <input
                id="city"
                className="city addTheatre-input"
                type="text"
                onChange={(e) => setCity(e.target.value)}
                required
              />
              <span className="highlight"></span>
              <span className="bar"></span>
              <label className="addTheatre-label">City</label>
            </div>
            <div className="group">
              <input
                id="technology"
                className="technology addTheatre-input"
                type="text"
                onChange={(e) => setTechnology(e.target.value)}
                required
              />
              <span className="highlight"></span>
              <span className="bar"></span>
              <label className="addTheatre-label">Technology</label>
            </div>

        

            <div className="group">
              <button className="btn-trans">Add</button>
            </div>
          </form>
        </div>
      </div>
      <Footer />
    </div>
  );
};

export default AddTheatre;
