import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import { React } from "react";
import ProtectedRoute from './protectedRoute';

//landing Page
import Home from "./home/home";

//user Login and Register
import Register from "./register/register";
import Login from "./login/login";

//Agent Login and Register
import AgentRegister from "./register/agentRegister/agentRegister";
import TheatreLogin from "./login/theatreAgent/theatreAgent";

//Admin Login and Register
import LoginAdmin from "./login/admin/admin";

// general pages
import Error404 from "./generalComponents/404/404";
import Contact from "./generalComponents/contactUS/contactUS";
import Theatres from "./theatres/theatres";

//user Pages
import TheatreListing from "./movies/theatreListing";

//Agent pages
import HomepageAgent from "./agent/homepage_agent/homepage_agent";
import AddTheatre from "./agent/addTheatre/addTheatre";

//admin pages
import HomepageAdmin from "./admin/adminHomepage/adminHomepage";
import AgentList from "./admin/agents/agents";
import UsersList from "./admin/users/users";
import TheatreList from "./admin/theatre/theatre";
import AddMovie from "./admin/addMovie/addMovie";
import MovieList from "./movies/movieList";



const Navigation = () => {
  return (
    <div>
      <Router>
        <Switch>
          <Route path="/" exact component={Home}></Route>
          <Route path="/login" exact component={Login}></Route>
          <Route path="/login/@admin" exact component={LoginAdmin}></Route>
          <Route path="/login/agent" exact component={TheatreLogin}></Route>
          <Route path="/register" exact component={Register}></Route>
          <Route path="/register/agent" exact component={AgentRegister}></Route>
          <Route path="/Theatres" exact component={Theatres}></Route>
          <Route path="/Contact" exact component={Contact}></Route>
          <ProtectedRoute path="/HomepageAgent" exact component={HomepageAgent}></ProtectedRoute>
          <ProtectedRoute path="/AddTheatre" exact component={AddTheatre}></ProtectedRoute>
          <ProtectedRoute path="/AddMovie" exact component={AddMovie}></ProtectedRoute>
          <ProtectedRoute path="/HomepageAdmin" exact component={HomepageAdmin}></ProtectedRoute>
          <ProtectedRoute path="/AgentList" exact component={AgentList}></ProtectedRoute>
          <ProtectedRoute path="/UsersList" exact component={UsersList}></ProtectedRoute>
        
          <Route path="/movies" exact component={MovieList}></Route>
          <Route path="/TheatreListing" exact component={TheatreListing}></Route>
          <ProtectedRoute path="/TheatreList" exact component={TheatreList}></ProtectedRoute>
          <Route path="*" exact component={Error404}></Route>

        </Switch>
      </Router>
    </div>
  );
};

export default Navigation;
