import { React, useState, useEffect } from "react";
import "./theatreCard.css";

import { CgAddR } from "react-icons/cg";
import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button'
const TheatreCard = () => {
  const [data, setData] = useState([]);

  console.log(data);
let id =localStorage.getItem("id")

  useEffect(() => {
    const theatreList = async () => {
      let response = await fetch(`http://localhost:5000/theatre/${id}`);
      let theatre = await response.json();
      setData(theatre);
    };
    theatreList();
  }, []);
  debugger

  return (
    <div>
      <div className="theatre-card-body">
        <div className="main">
          <a href="/AddTheatre">
            <CgAddR style={{ fontSize: "40px" }} />
          </a>

          <h1 className="theatre-listing-header"> Theatre listing</h1>
      
          { data.map((theatre) => (id ===`${theatre._id}`? <Card className="theatre-listing-card"  key={theatre._id}>
            <Card.Header className="h5">{theatre.theatre_name}</Card.Header>
            <Card.Body>
              <Card.Title>Special title treatment</Card.Title>
              <Card.Text>
                With supporting text below as a natural lead-in to additional
                content.
              </Card.Text>
              <Button className="theatre-card-button" variant="primary">Go somewhere</Button>
            </Card.Body>
          </Card>:<div></div>) )}

   
 

 
        
        </div>
      </div>
    </div>
  );
};

export default TheatreCard;
